    R("stug-16x16", () => X.stug(16, 16, "", 8));
    R("stug?", (a) => X.alias("stug-" + a + "x" + a));
    R("stug2-?x?", (a, b) => X.stug(a, b, "2"));
    R("stug3-?x?", (a, b) => X.stug(a, b, "3"));
    R("stug3-1x11", (a, b) => X.stugX(11, [-50, 6, 60, 5]));
    R("stug4-?x?", (a, b) => X.stug(a, b, "4"));
